<template>
  <transition name="rightMove">
    <div v-show="showFlag" class="food" ref="food">
      <div class="food-content">
        <div class="image-header">
          <img :src="food.image" alt="">
          <div class="back" @click="hide">
            <i class="icon-arrow_lift"></i>
          </div>

        </div>
        <div class="content">
          <div class="title">{{food.name}}</div>
          <div class="detail">
            <span class="sell-count">月售{{food.sellCount}}</span>
            <span class="rating">好评率{{food.rating}}</span>
          </div>
          <div class="price">
            <span class="now-price">￥{{food.price}}</span><span class="old-price" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
          </div>
          <div class="cartcontrol-wrapper">
            <cartcontrol @cartadd="_drop" :food="food"></cartcontrol>
          </div>
          <transition name="fade">
            <div @click.stop.prevent="addFirst" class="buy" v-show="!food.count||food.count===0">加入购物车</div>
          </transition>
        </div>
        <split v-show="food.info"></split>
        <div class="info" v-show="food.info">
          <div class="title">商品信息</div>
          <p class="text">{{food.info}}</p>
        </div>
        <split></split>
        <div class="rating">
          <h1 class="title">商品评价</h1>
          <ratingselect :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"
                        @change-type="changeType" @toggle-content="toggleContent"></ratingselect>
          <div class="rating-wrapper">
            <ul v-show="food.ratings && food.ratings.length">
              <li v-show="needShow(rating.rateType,rating.text)" v-for="(rating,index) in food.ratings"
                  class="rating-item" :key="index">
                <div class="user">
                  <span class="name">{{rating.username}}</span>
                  <img :src="rating.avatar" width="12" height="12" class="avatar">
                </div>
                <div class="time">{{rating.rateTime | formatDate}}</div>
                <p class="text">
                  <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>
                  <span class="rating-text">{{rating.text}}</span>
                </p>
              </li>
            </ul>
            <div class="no-rating" v-show="!food.ratings||!food.ratings.length">暂无评价</div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  import cartcontrol from 'components/cartcontrol/cartcontrol'
  import Vue from 'vue'
  import split from 'components/split/split'
  import ratingselect from '../ratingselect/ratingselect'
  import {formatDate} from 'src/common/js/date'
  // const POSITIVE = 0
  // const NEGATIVE = 1
  const ALL = 2

  export default {
    components: {
      ratingselect,
      cartcontrol,
      split
    },
    name: 'food',
    props: {
      food: {
        type: Object
      }
    },
    data() {
      return {
        showFlag: false,
        selectType: ALL,
        onlyContent: true,
        desc: {
          all: '全部',
          positive: '推荐',
          negative: '吐槽'
        }
        // count: this.food.count
      }
    },
    watch: {
      // count() {
      //   console.log('11')
      // }
    },
    methods: {
      needShow(type, text) {
        if (this.onlyContent && !text) {
          return false
        }
        if (this.selectType === ALL) {
          return true
        } else {
          return this.selectType === type
        }
      },
      changeType(type) {
        this.selectType = type
      },
      toggleContent(toggle) {
        this.onlyContent = toggle
      },
      _drop(target) {
        // this.$refs.shopcart.drop_(target)
        this.$emit('cartadd', target)
        // console.log(target)
      },
      show() {
        this.showFlag = true
        // this.selectType = ALL
        // this.onlyContent = true
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.food, {click: true})
          } else {
            this.scroll.refresh()
          }
        })
      },
      hide() {
        this.showFlag = false
      },
      addFirst(event) {
        /* 防止多次点击 */
        // if (!event._constructed) {
        //     return
        // }
        Vue.set(this.food, 'count', 1)
        this.$emit('cartadd', event.target)
      }
    },
    filters: {
      formatDate(time) {
        let date = new Date(time)
        return formatDate(date, 'yyyy-MM-dd hh:mm')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .food
    position fixed
    left 0
    top 0
    bottom 48px
    z-index 30
    width 100%
    background #fff
    .food-content
      .image-header
        position relative
        width 100%
        height 0
        padding-top 100%
        img
          position absolute
          top 0
          left 0
          width 100%
          height 100%
        .back
          position absolute
          top 10px
          left 0
          .icon-arrow_lift
            display block
            padding 10px
            font-size 20px
            color #fff
      .content
        position relative
        padding 18px
        .title
          font-weight 700
          line-height 14px
          font-size 14px
          margin-bottom 8px
          color rgb(7, 17, 27)
        .detail
          margin-bottom 18px
          line-height 10px
          font-size 0
          height 10px
          .sell-count, .rating
            font-size 10px
            color rgb(147, 153, 159)
          .sell-count
            margin-right 12px
        .price
          line-height 24px
          .now-price
            font-weight 700
            margin-right 8px
            font-size 14px
            color rgb(240, 20, 20)
          .old-price
            text-decoration line-through
            font-size 10px
            color rgb(147, 153, 159)

        .cartcontrol-wrapper
          position absolute
          right 12px
          bottom 12px
        .buy
          position absolute
          right 18px
          bottom 18px
          z-index 10
          height 24px
          line-height 24px
          box-sizing border-box
          border-radius 12px
          font-size 10px
          padding 0 12px
          color #fff
          background rgb(0, 160, 220)
          &.fade-enter-active, .fade-leave-active {
            transition: all 0.5s;
            opacity: 1
          }
          &.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
            opacity: 0
          }

      .info
        padding 18px
        .title
          line-height 14px
          margin-bottom 16px
          font-size 14px
          font-weight 700
          color rgb(7, 17, 27)
        .text
          line-height 24px
          padding 0 8px
          font-size 12px
          color rgb(77, 85, 93)
          text-indent 2em

      .rating
        padding-top 18px
        .title
          font-weight 700
          line-height 14px
          font-size 14px
          margin-left 18px
          color rgb(7, 17, 27)
        .rating-wrapper
          padding 0 18px
          .rating-item
            position relative
            padding 16px 0
            border-1px(rgba(7, 17, 27, 0.1))
            .user
              position absolute
              right 0
              top 16px
              line-height 12px
              font-size 0px
              .name
                display inline-block
                margin-right 6px
                vertical-align top
                font-size 10px
                color rgb(147, 153, 159)
              .avatar
                border-radius 50%

            .time
              margin-bottom 6px
              line-height 12px
              font-size 10px
              color rgb(147, 153, 159)
            .text
              line-height 16px
              font-size 12px
              color rgb(7, 17, 27)
              .icon-thumb_up, .icon-thumb_down
                margin-right 4px
                vertical-align middle
                line-height 24px
                font-size 12px
              .icon-thumb_up
                color rgb(0, 160, 220)
              .icon-thumb_down
                color rgb(147, 153, 159)

          .no-rating
            padding 16px 0
            font-size 12px
            color rgb(147, 153, 159)

  .rightMove-enter-active, .rightMove-leave-active
    transition all 0.3s linear
    transform translate3d(0, 0, 0)

  .rightMove-enter, .rightMove-leave-to
    transform translate3d(100%, 0, 0)

  // left 100%
</style>



