<template>
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title">综合评分</div>
          <div class="rank">高于周边{{seller.rankRate}}%商家</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title">服务态度</span>
            <star :size="36" :score="seller.score"></star>
            <span class="score">{{seller.score}}</span>
          </div>
          <div class="score-wrapper">
            <span class="title">商品评分</span>
            <star :size="36" :score="seller.foodScore"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrapper">
            <span class="title">送达时间</span>
            <span class="deliveryTime">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>

      </div>
      <split></split>
      <ratingselect :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="ratings"
                    @change-type="changeType" @toggle-content="toggleContent"></ratingselect>
      <div class="rating-wrapper">
        <ul>
          <li v-show="needShow(rating.rateType,rating.text)" class="rating-item" v-for="(rating,index) in ratings" :key="index">
            <div class="avatar">
              <img width="28" height="28" :src="rating.avatar" alt="">
            </div>
            <div class="content">
              <h1 class="name">{{rating.username}}</h1>
              <div class="star-wrapper">
                <star class="star" :size="24" :score="rating.score"></star>
                <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span>
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend||!rating.recommend.length">
                <span class="icon-thumb_up"></span>
                <span class="item" :key="index" v-for="(item,index) in rating.recommend">{{item}}</span>
              </div>
              <div class="time">{{rating.rateTime | formatDate }}</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import star from 'components/star/star'
  import split from 'components/split/split'
  import ratingselect from 'components/ratingselect/ratingselect'
  import {formatDate} from 'src/common/js/date'
  import Bscroll from 'better-scroll'

  const ERR_OK = 0
  const ALL = 2
  export default {
    components: {star, split, ratingselect},
    name: 'ratings',
    data() {
      return {
        ratings: [],
        selectType: ALL,
        onlyContent: true,
        desc: {
          all: '全部',
          positive: '推荐',
          negative: '吐槽'
        }
      }
    },
    methods: {
      needShow(type, text) {
        if (this.onlyContent && !text) {
          return false
        }
        if (this.selectType === ALL) {
          return true
        } else {
          return this.selectType === type
        }
      },
      changeType(type) {
        this.selectType = type
      },
      toggleContent(toggle) {
        this.onlyContent = toggle
      }
    },
    created() {
      this.$http.get('/api/ratings').then((response) => {
        response = response.body
        if (response.errno === ERR_OK) {
          this.ratings = response.data
          this.$nextTick(() => {
            this.scroll = new Bscroll(this.$refs.ratings, {
              click: true
            })
            // this._initScroll()
            // this._calculateHeight()
          })
        }
      })
    },
    filters: {
      formatDate(time) {
        let date = new Date(time)
        return formatDate(date, 'yyyy-MM-dd hh:mm')
      }
    },
    props: {
      seller: {
        type: Object
      },
      goods: {
        type: Array
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .ratings
    position absolute
    top 186px
    bottom 52px
    left 0
    width 100%
    overflow hidden
    .overview
      display flex
      padding 12px 0
      .overview-left
        flex 0 0 110px
        width 110px
        padding 6px 0
        text-align center
        border-right 1px solid rgba(7, 17, 27, 0.1)
        @media only screen and (max-width 340px)
          flex 0 0 95px
          width 95px
        .score
          line-height 28px
          font-size 24px
          color rgb(255, 153, 30)
          margin-bottom 6px
        .title
          margin-bottom 8px
          line-height 12px
          font-size 12px
          color rgb(7, 17, 27)
        .rank
          line-height 10px
          font-size 10px
          color rgb(147, 153, 159)
      .overview-right
        flex 1
        padding 6px 0 6px 14px
        @media only screen and (max-width 340px)
          padding 6px 0 6px 4px
        .score-wrapper
          margin-bottom 8px
          font-size 0
          .title
            display inline-block
            vertical-align top
            line-height 18px
            font-size 12px
            font-weight 700
            color rgb(7, 17, 27)
          .star
            vertical-align top
            display inline-block
            margin 0 12px
          .score
            vertical-align top
            line-height 18px
            display inline-block
            font-size 12px
            color rgb(255, 153, 30)

        .delivery-wrapper
          font-size 0
          text-align left
          .title
            line-height 18px
            font-size 12px
            font-weight 700
            color rgb(7, 17, 27)
          .deliveryTime
            font-size 12px
            line-height 18px
            margin 0 6px
            color rgb(147, 153, 159)
    .rating-wrapper
      padding 0 18px
      .rating-item
        display flex
        padding 18px 0
        border-1px()
        .avatar
          flex 0 0 28px
          margin-right 12px
          img
            border-radius 50%
        .content
          position relative
          flex 1
          .name
            line-height 12px
            font-size 10px
            color rgb(7, 17, 27)
          .star-wrapper
            margin-bottom 6px
            font-size 0
            .star
              display inline-block
              margin-right 6px
              vertical-align top
            .delivery
              line-height 12px
              font-size 10px
              color rgb(147, 153, 159)

          .text
            line-height 18px
            font-size 12px
            margin-bottom 8px
            color rgb(7, 17, 27)
          .recommend
            line-height 16px
            .icon-thumb_up, .item
              display inline-block
              line-height 18px
              font-size 12px
            .icon-thumb_up
              vertical-align middle
              color rgb(0, 160, 220)
            .item
              margin 0 8px 4px 0
              border-radius 6px
              padding 0 6px
              border 1px solid rgba(7, 17, 27, 0.1)
              background  rgba(0, 160, 220,0.1)
          .time
            position absolute
            top 0
            right 0
            font-size 12px
            color rgb(147, 153, 159)
</style>



